.projects {
  position:relative;
  padding: 53px 4.4% 90px;
  background:$_white;
}

@include media-tablet  {
  .projects__box {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
}


.projects__title {
  margin: 0;
  padding: 0 0 12px;
  font-family: $_f__common;
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 69px;
  color: $_secondary_black;
}

.projects__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;

  @include media-tablet  {
    justify-content: end;
    margin-top: -70px;
  }
}


.projects__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 50px;
  padding: 12px 36px;

  background: $_button_dark;
  border-radius: 64px;

  font-family: $_f__common;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: $_white;
}
