.about-us {
  position: relative;
  padding: 105px 4.4% 198px;
  background-color: $_main_dark;
}

.about-us__title {
  position: relative;
  padding: 0 40px 40px 0;
  margin: 0;
  font-family: $_f__common;
  font-style: normal;
  font-weight: 400;
  font-size: 55px;
  line-height: 52px;
  color: $_main_light;

  @include media-tablet  {
    font-size: 75px;
    line-height: 78px;
  }
}

.about-us__content {
  position: relative;
  margin: 0;
  padding: 0 35px 0 0;
  font-family: $_f__common;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  color: $_main_light;

  @include media-tablet  {
    font-size: 28px;
    line-height: 128%;
  }

  &::before {
    content: "";
    position: absolute;
    right: 0;
    width: 660px;
    height: 660px;
    background-image: url("data:image/svg+xml, %3Csvg fill='none' stroke='%235D9F93' xmlns='http://www.w3.org/2000/svg' id='icon-plus' viewBox='0.9 0.9 24.2 24.2'%3E%3Cg stroke-width='0.07' fill-rule='evenodd'%3E%3Cg id='Learn' transform='translate(-249.000000, -3678.000000)'%3E%3Cg id='Group-6' transform='translate(250.000000, 3679.000000)'%3E%3Cpath d='M24,24 L12.0006916,24 C5.40141779,24 0,18.5999654 0,12.0006916 C0,5.40141779 5.40141779,0 12.0006916,0 C18.5999654,0 24,5.40141779 24,12.0006916 L24,24 Z' stroke-width='0.07'/%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
    bottom: -40px;
    background-position: bottom right;
    background-size: 100% 100%;
  }
}



